
.card-company {
  margin: 0 auto 32px;
  display: -ms-flexbox;
  display: box;
  display: flex;
  -webkit-box-orient: vertical;
  -moz-box-orient: vertical;
  -o-box-orient: vertical;
  -webkit-box-lines: single;
  -moz-box-lines: single;
  -o-box-lines: single;
  -ms-flex-flow: column nowrap;
  flex-flow: column nowrap;
  -o-box-align: stretch;
  -ms-flex-align: stretch;
  align-items: stretch;
  padding: 0 16px;
  max-width: 960px;
}
.card-company .photo,
.card-company .title,
.card-company .bio {
  margin-bottom: 16px;
}
.card-company .photo img {
  border: 1px solid #dedede;
  padding: 4px;
  display: block;
  max-width: 224px;
  max-height: 224px;
  margin: 0 auto;
}
.card-company .name,
.card-company .title {
  text-align: center;
}
.card-company .name {
  font-weight: bold;
}
.card-company .title {
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 0.0625em;
  color: #aaa;
}
.card-company .ids a {
  display: block;
  border-top: 1px dotted #dedede;
  padding: 4px;
  width: 100%;
}
.card-company .ids a i.fa {
  display: inline-block;
  width: 28px;
  text-align: center;
}
.card-company .ids a:hover .label {
  text-decoration: underline;
}
@media screen and (min-width: 720px) {
.card-company {
    -webkit-box-orient: horizontal;
    -moz-box-orient: horizontal;
    -o-box-orient: horizontal;
    -ms-flex-flow: row;
    flex-flow: row;
    margin-bottom: 48px;
}
.card-company .photo {
    margin-bottom: 0;
}
.card-company .data {
    -o-box-flex: 1;
    box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    padding-left: 32px;
}
.card-company .data .name,
  .card-company .data .title {
    text-align: left;
}
.card-company .data .name {
    font-size: 20px;
}
.card-company .data .title {
    font-size: 16px;
}
}
@media screen and (min-width: 960px) {
.card-company .data {
    display: -ms-flexbox;
    display: box;
    display: flex;
    -webkit-box-orient: vertical;
    -moz-box-orient: vertical;
    -o-box-orient: vertical;
    -webkit-box-lines: single;
    -moz-box-lines: single;
    -o-box-lines: single;
    -ms-flex-flow: column nowrap;
    flex-flow: column nowrap;
}
}


