
.card-post {
  border-bottom: 1px dotted #dedede;
  margin: 0 auto;
}
.card-post:first-of-type {
  margin-top: -24px;
}
.card-post:last-of-type {
  border-bottom: none;
}
.card-post .title {
  font-weight: 500;
  line-height: 1.25;
  color: #000;
}
.card-post .meta {
  text-transform: uppercase;
  font-size: 0.75em;
  font-weight: 500;
  line-height: 2;
  color: #aaa;
}
.card-post .desc {
  max-width: 40em;
}
.card-post a {
  padding: 24px 0;
  display: block;
  color: #000;
}
.card-post a:hover .title {
  color: #2273c3;
}
@media screen and (min-width: 360px) {
.card-post .title {
    font-size: 1.125em;
}
.card-post .meta {
    font-size: 0.85em;
}
}
@media screen and (min-width: 720px) {
.card-post {
    font-size: 18px;
}
.card-post a {
    padding: 32px 0;
}
}


