
.section-install {
  background: #0e2e4e;
  border-bottom: 1px solid #144372;
  padding: 32px 16px;
}
.section-install h2 {
  color: #a6cbef;
  font-weight: 500;
  font-size: 20px;
  text-align: center;
  line-height: 1;
  margin-bottom: 24px;
}
.section-install .btn {
  margin: 0 auto;
  max-width: 256px;
}
@media screen and (min-width: 720px) {
.section-install {
    padding: 48px 16px;
}
.section-install h2 {
    font-size: 24px;
}
}


