
.app-footer {
  background: #0e2e4e;
  border-bottom: 4px solid #091f34;
}
.app-footer .footer-logo {
  width: 80px;
  height: 80px;
}
.app-footer .sections {
  padding: 16px 0;
  color: #fff;
  margin: 0 auto;
  display: -ms-flexbox;
  display: box;
  display: flex;
  -webkit-box-orient: horizontal;
  -moz-box-orient: horizontal;
  -o-box-orient: horizontal;
  -webkit-box-lines: multiple;
  -moz-box-lines: multiple;
  -o-box-lines: multiple;
  -ms-flex-flow: row wrap;
  flex-flow: row wrap;
}
.app-footer section {
  -o-box-flex: 1;
  box-flex: 1;
  -ms-flex: 0 0 50%;
  flex: 0 0 50%;
  padding: 16px 8px;
}
.app-footer section header {
  padding: 4px 8px;
  color: #5fa1e3;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.0625em;
}
.app-footer section a,
.app-footer section span {
  color: #dbeaf9;
  display: block;
  padding: 4px 8px;
}
.app-footer section a i.fa,
.app-footer section span i.fa {
  display: inine-block;
  text-align: center;
  width: 24px;
  margin-right: 8px;
}
@media screen and (min-width: 360px) {
.app-footer .sections {
    padding: 16px 8px;
}
.app-footer .section {
    padding: 16px;
}
}
@media screen and (min-width: 400px) {
.app-footer .sections {
    padding: 16px;
}
}
@media screen and (min-width: 720px) {
.app-footer .sections {
    padding: 32px;
}
.app-footer section {
    -o-box-flex: 1;
    box-flex: 1;
    -ms-flex: 0 0 25%;
    flex: 0 0 25%;
}
.app-footer section header {
    margin-bottom: 16px;
}
}
@media screen and (min-width: 960px) {
.app-footer .sections {
    max-width: 960px;
}
.app-footer section {
    -o-box-flex: 1;
    box-flex: 1;
    -ms-flex: 0 0 25%;
    flex: 0 0 25%;
}
}
@media screen and (min-width: 1200px) {
.app-footer .sections {
    max-width: 1200px;
}
.app-footer section {
    -o-box-flex: 1;
    box-flex: 1;
    -ms-flex: 0 0 25%;
    flex: 0 0 25%;
}
}


