
.app-header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  background: #0e2e4e;
  width: 100vw;
  display: -ms-flexbox;
  display: box;
  display: flex;
  height: 38.4px;
}
.app-header a.nav-home {
  display: block;
  border-top: 3px solid #091f34;
  border-top-color: #00862d !important;
}
.app-header a.nav-home img {
  width: 35.4px;
  height: 35.4px;
}
.app-header a.nav-home:hover {
  border-top-color: #00862d !important;
}
.app-header nav {
  -o-box-flex: 1;
  box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  display: -ms-flexbox;
  display: box;
  display: flex;
  -o-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end;
  border-top: 3px solid #091f34;
  padding-right: 8px;
}
.app-header nav a {
  display: block;
  padding: 0 8px;
  color: #a6cbef;
  border-top: 3px solid #091f34;
  margin-top: -3px;
  line-height: 35.4px;
  font-size: 14.4px;
}
.app-header nav a .label {
  display: none;
}
.app-header nav a:hover {
  border-top-color: #2273c3 !important;
  color: #dbeaf9;
}
.app-header nav a.router-link-active {
  border-top-color: #2273c3 !important;
}
@media screen and (min-height: 400px) {
.app-header {
    height: 48px;
}
.app-header a.nav-home {
    border-top: 4px solid #091f34;
}
.app-header a.nav-home img {
    width: 44px;
    height: 44px;
}
.app-header nav {
    border-top: 4px solid #091f34;
}
.app-header nav a {
    border-top: 4px solid #091f34;
    margin-top: -4px;
    line-height: 44px;
}
}
@media screen and (min-width: 360px) {
.app-header nav a {
    font-size: 16px;
}
.app-header nav a.nav-code {
    display: block;
}
}
@media screen and (min-width: 400px) {
.app-header nav {
    padding-left: 12px;
}
.app-header nav a {
    padding: 0 12px;
}
}
@media screen and (min-width: 480px) {
.app-header nav a.nav-media {
    display: block;
}
}
@media screen and (min-width: 640px) {
.app-header nav a {
    padding: 0 24px;
}
}
@media screen and (min-width: 720px) {
.app-header nav a i.fa {
    margin-right: 4px;
}
.app-header nav a .label {
    display: inline;
}
}


