
.page-presentations-entry .youtube {
  display: block;
  width: 100vw;
  height: 56.25vw;
  max-width: 768px;
  max-height: 432px;
  margin-left: -16px;
  margin-right: -16px;
}
.page-presentations-entry div.youtube {
  position: relative;
  cursor: pointer;
  background: #000 no-repeat center center;
  background-size: cover;
  clear: both;
}
.page-presentations-entry div.youtube:before {
  content: '';
  background: rgba(0,0,0,0.7);
  height: 64px;
  width: 96px;
  display: block;
  margin: -32px 0 0 -48px;
  border-radius: 16px;
  position: absolute;
  top: 50%;
  left: 50%;
  transition: all 100ms ease;
  z-index: 1;
}
.page-presentations-entry div.youtube:hover:before {
  background: rgba(0,0,0,0.85);
}
.page-presentations-entry div.youtube:after {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2;
  margin-left: -16px;
  margin-top: -16px;
  width: 32px;
  line-height: 32px;
  content: '\f04b';
  font-size: 32px;
  font-family: FontAwesome;
  text-align: center;
  color: #fff;
}
.page-presentations-entry div.youtube iframe {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
}
@media screen and (min-width: 360px) {
.page-presentations-entry .youtube {
    margin-left: -24px;
    margin-right: -24px;
}
}
@media screen and (min-width: 768px) {
.page-presentations-entry .youtube {
    margin-bottom: 1.5em;
    margin-left: -20px;
    margin-right: -20px;
}
.page-presentations-entry div.youtube {
    margin-bottom: 0;
}
}
@media screen and (min-width: 800px) {
.page-presentations-entry .youtube {
    margin-left: auto;
    margin-right: auto;
}
}


